const oldArrProto = Array.prototype
export const arrayMethods = Object.create(oldArrProto);
// Object.create ä½¿ç”¨ç°æœ‰çš„å¯¹è±¡æ¥ä½œä¸ºæ–°åˆ›å»ºå¯¹è±¡çš„åŸå‹
const methods = [
  'push',
  'unshift',
  'splice',
  'pop',
  'reserve',
  'sort',
  'shift'
]
// configurable:false,//èƒ½å¦ä½¿ç”¨deleteã€èƒ½å¦éœ€æ”¹å±æ€§ç‰¹æ€§ã€æˆ–èƒ½å¦ä¿®æ”¹è®¿é—®å™¨å±æ€§ã€ï¼Œfalseä¸ºä¸å¯é‡æ–°å®šä¹‰ï¼Œé»˜è®¤å€¼ä¸ºtrue
// enumerable:false,//å¯¹è±¡å±æ€§æ˜¯å¦å¯é€šè¿‡for-inå¾ªç¯ï¼Œflaseä¸ºä¸å¯å¾ªç¯ï¼Œé»˜è®¤å€¼ä¸ºtrue
// writable:false,//å¯¹è±¡å±æ€§æ˜¯å¦å¯ä¿®æ”¹,flaseä¸ºä¸å¯ä¿®æ”¹ï¼Œé»˜è®¤å€¼ä¸ºtrue

// ç”¨æˆ·è°ƒç”¨çš„æ˜¯ä»¥ä¸Šä¸ƒä¸ªæ–¹æ³•ä¼šç”¨è‡ªå·±é‡å†™çš„ï¼Œå¦åˆ™å°±ç”¨åŸæ¥çš„æ•°ç»„åŸå‹ä¸Šé¢çš„æ–¹æ³•
methods.forEach(method =>{
  // NOTE ç¼“å­˜åŸå§‹æ–¹æ³•
  const original = oldArrProto[method]
  // ä»£ç†
  Object.defineProperty(arrayMethods,method,{
    value:function mutator(...args){
      const result = original.call(this,...args)
      const ob = this.__ob__
      let inserted;
      switch(method){
        case 'push':
        case 'unshift':
          inserted = args;
          break;
        case 'splice':
          inserted = args.slice(2)
          break;
        default:
          break;
      }
      // å¦‚æœæœ‰æ–°å¢åŠ çš„å€¼è¦ç»§ç»­è¿›è¡ŒåŠ«æŒ
      if(inserted) ob.observerArray(inserted)
      ob.dep.notify()
      return result
    },
    enumerable:false,
    writable:false,
    configurable:false
  })
    
    
})


