import { isObject,def,isValidArrayIndex,isArray,hasOwn} from "../../util"
import { arrayMethods } from './array'
import Dep from './dep'

// NOTE æ£€æŸ¥__ob__æ˜¯å¦å¯ç”¨
const hasProto = '__ob__' in {}
const arrKeys = Object.getOwnPropertyNames(arrayMethods)

//INFO è§‚æµ‹è€…ç±» ç±»æœ‰ç±»å‹ï¼Œå¯¹è±¡æ²¡æœ‰ç±»å‹
class Observer {
  constructor(value) {
    this.value = value,
    this.dep = new Dep()
    def(value,'__ob__',this)

    // value.__ob__ = this;//æ‰€æœ‰è¢«åŠ«æŒçš„å±æ€§éƒ½æœ‰ __ob__
    // NOTE 2ã€å¯¹valueä¸­çš„æ•°æ®è¿›è¡Œéå†å¾ªç¯ åŠ«æŒ 
    if(Array.isArray(value)){ //æ•°ç»„åŠ«æŒ 
      // å†…éƒ¨å¯¹æ•°ç»„ä¸é‡‡ç”¨ Object.defineProperty
      // å¯¹æ•°ç»„åŸæ¥çš„æ–¹æ³•è¿›è¡Œæ”¹å†™ push shift pop unshift reserve sort splice/ 
      // __proto__æŒ‡å‘æ„é€ å‡½æ•°çš„åŸå‹ï¼ˆprototypeï¼‰å¯¹è±¡ 
      const augment = hasProto ? protoAugment : copyAugment
      augment(value,arrayMethods,arrKeys)

      // TAG å¦‚æœæ•°ç»„é‡Œé¢çš„æ˜¯å¯¹è±¡ï¼Œåˆ™éœ€è¦åŠ«æŒå¯¹è±¡
      this.observerArray(value)

    }else{
      this.walk(value) //å¯¹è±¡åŠ«æŒ
    }
    
  }
  // éå†å¯¹è±¡
  walk(value) {
    // NOTE 3ã€éå†å¯¹è±¡ï¼Œå“åº”å¼åŠ«æŒ
    Object.keys(value).forEach((key)=> defineReactive(value,key,value[key]))
  }
  // éå†æ•°ç»„
  observerArray(value){
    value.forEach((item)=> observer(item))
  }

}

function defineReactive(data, key, value) {
  // NOTE 4ã€valueæœ‰å¯èƒ½æ˜¯å¯¹è±¡ï¼Œå†è¿›è¡Œé€’å½’åŠ«æŒ
  const childOb = observer(value)
  // æ¯ä¸ªå±æ€§éƒ½å¯¹åº”ä¸€ä¸ª dep
  let dep = new Dep()
  Object.defineProperty(data, key,{
    get(){
      // å–å€¼çš„æ—¶å€™å°†wtacherå’Œdepå…³è”èµ·æ¥
      // Dep.target
      if(window.target){
        // æ”¶é›†å¯¹è±¡ä¾èµ–(watcher)
        dep.depend()
        // æ”¶é›†æ•°ç»„ä¾èµ–(watcher)
        if(childOb) childOb.dep.depend()
      }
      return value;
    },
    set(newValue){
      // NOTE 5ã€å¦‚æœç”¨æˆ·èµ‹å€¼ä¸€ä¸ªæ–°çš„å¯¹è±¡éœ€è¦å°†è¿™ä¸ªå¯¹è±¡ä¹Ÿè¿›è¡ŒåŠ«æŒ
      // TAGï¼šåªæœ‰æ–°å¢åŠ çš„å±æ€§æ˜¯å¯¹è±¡æ‰ä¼šè¿›è¡Œç›‘å¬åŠ«æŒ
      if(newValue !== value){
        observer(newValue)
        // (å¯¹è±¡è§¦å‘æ”¶é›†çš„ watcher )å‘Šè¯‰å½“å‰çš„å±æ€§å­˜æ”¾çš„wtacheræ‰§è¡Œget()
        dep.notify()
        value = newValue
      }
    }
  })
}

function protoAugment(target,src,keys){
  target.__ob__ = src
}
function copyAugment(target,src,keys){
  for(var i=0;i<keys.length;i++){
    const key = keys[i]
    def(target,key,src[key])
  }
}

export function set(target,key,val) {
  // targetæ˜¯æ•°ç»„å¹¶ä¸”keyæ˜¯ä¸€ä¸ªæœ‰æ•ˆçš„ç´¢å¼•å€¼ï¼Œå°±å…ˆè®¾ç½®lengthå±æ€§
  if (isArray(target) && isValidArrayIndex(key)) {
    target.length = Math.max(target.length, key)
    target.splice(key, 1, val)
    return val
  }
  // ç”±äºkeyå·²ç»å­˜åœ¨äºtargetä¸­ï¼Œæ‰€ä»¥å…¶å®è¿™ä¸ªkeyå·²ç»è¢«ä¾¦æµ‹äº†å˜åŒ–ã€‚ä¹Ÿå°±æ˜¯è¯´ï¼Œè¿™ç§æƒ…å†µå±äºä¿®æ”¹æ•°æ®ï¼Œç›´æ¥ç”¨keyå’Œvalæ”¹æ•°æ®å°±å¥½äº†
  if (key in target && !(key in Object.prototype)) {
    target[key] = val
    return val
  }
  const ob = target.__ob__;
  // é‚£ä¹ˆï¼Œä»€ä¹ˆæ˜¯æ ¹æ•°æ®ï¼Ÿthis.$dataå°±æ˜¯æ ¹æ•°æ®ã€‚
  if (target._isVue || (ob && ob.vmCount)) {
      console.warn('targetä¸èƒ½æ˜¯Vue.jså®ä¾‹æˆ–Vue.jså®ä¾‹çš„æ ¹æ•°æ®å¯¹è±¡')
    return val
  }
  // ä¸æ˜¯å“åº”å¼æ•°æ®ç›´æ¥èµ‹å€¼å°±è¡Œ
  if (!ob) {
    target[key] = val
    return val
  }
  // NOTE å¤„ç†æ–°å¢çš„å±æ€§
    /**
   * å¦‚æœå‰é¢çš„æ‰€æœ‰åˆ¤æ–­æ¡ä»¶éƒ½ä¸æ»¡è¶³ï¼Œé‚£ä¹ˆè¯´æ˜ç”¨æˆ·æ˜¯åœ¨å“åº”å¼æ•°æ®ä¸Š
   * æ–°å¢äº†ä¸€ä¸ªå±æ€§ï¼Œè¿™ç§æƒ…å†µä¸‹éœ€è¦è¿½è¸ªè¿™ä¸ªæ–°å¢å±æ€§çš„å˜åŒ–ï¼Œå³ä½¿ç”¨
   * defineReactiveå°†æ–°å¢å±æ€§è½¬æ¢æˆgetter/setterçš„å½¢å¼å³å¯ã€‚
   */
  defineReactive(ob.value, key, val)
  // å‘targetçš„ä¾èµ–è§¦å‘å˜åŒ–é€šçŸ¥ï¼Œå¹¶è¿”å›val
  ob.dep.notify()
  return val
}

export function del(target, key){
  // æ•°ç»„
  if(Array.isArray(target) && isValidArrayIndex(key)){
    target.splice(key, 1)
    return
  }
  
  // å¯¹è±¡
  const ob = target.__ob__;

  if (target._isVue || (ob && ob.vmCount)) {
    console.warn('targetä¸èƒ½æ˜¯Vue.jså®ä¾‹æˆ–Vue.jså®ä¾‹çš„æ ¹æ•°æ®å¯¹è±¡')
    return 
  }
  // å¦‚æœä¸æ˜¯targetè‡ªå·±çš„å±æ€§å°±é˜»æ­¢ç¨‹åºç»§ç»­æ‰§è¡Œ
  if(!hasOwn(target, key)) return

  delete target[key]
  // åªæœ‰å“åº”å¼æ•°æ®æ‰éœ€è¦å‘é€é€šçŸ¥
  if(!ob) return
  ob.dep.notify()
}


export function observer(value){
  // NOTE: 1ã€å¦‚æœæ˜¯å¯¹è±¡æ‰è¿›è¡Œè§‚æµ‹
  if(!isObject(value)) return 
  // NOTE: 2ã€å¦‚æœæ˜¯å“åº”å¼çš„ä¹Ÿæ²¡å¿…è¦å†è¿›è¡Œè§‚æµ‹äº†

  let ob = null;
  if(value.__ob__ && value.__ob__ instanceof Observer){
    ob = value.__ob__
  }else{
    ob = new Observer(value)
  }
  return ob
}