function proxy(vm,source,key){
  Object.defineProperty(vm,key,{
    get() {
      return vm[source][key]
    },
    set(newValue){
      vm[source][key] = newValue
    }
  })
}

function initData(vm){
  let data = vm.$options.data
  // INFO: Vue2ä¸­ä¼šå°†dataä¸­æ‰€æœ‰çš„æ•°æ®è¿›è¡ŒåŠ«æŒ Object.defineProperty
  if(!isFunction(data)){
  }else{
    // TAG ç»‘å®šthis é€šè¿‡_dataè¿›è¡Œå…³è”
    data = vm._data = data.call(vm)
  }
  // TAG ç”¨æˆ· vm.name ä»£ç†--> vm._data.name 
  for(let key in data){
    proxy(vm,'_data',key)
  }
  // NOTE: 1ã€æŠŠ data å˜æˆå“åº”å¼
  observer(data)
} 




export function stateMixin(Vue) {
  // Vue.jsä¸­è®¡ç®—å±æ€§ï¼ˆComputedï¼‰çš„å®ç°åŸç†ä¸expOrFnæ”¯æŒå‡½æ•°æœ‰å¾ˆå¤§çš„å…³ç³»

  Vue.prototype.$set = set
  Vue.prototype.$delete = del

  Vue.prototype.$watch = function (expOrFn,cb,options) {
    const vm = this
    options = options || {}
    options.user = true
    const watcher = new Watcher(vm, expOrFn, cb, options)
    // NOTE: 1.ä»£ç ä¼šåˆ¤æ–­ç”¨æˆ·æ˜¯å¦ä½¿ç”¨äº†immediateå‚æ•°ï¼Œå¦‚æœä½¿ç”¨äº†ï¼Œåˆ™ç«‹å³æ‰§è¡Œä¸€æ¬¡cbã€‚
    if (options.immediate) {
      cb.call(vm,watcher.value)
    }
    return function unwatchFn() {
      watcher.teardown()
    }
  }
}


